.bg{
    background: #000;
    padding: 3% 0;
}
.flexDiv{
    display: flex;
    justify-content: space-between;
    padding:2% 5% ;
}
.contact{
    color: #fff;
    font-weight: bold;
    font-size: 2.8em;
}
.links{
    text-decoration: underline;
    color: #e6c401;
    font-weight: 800;
    line-height: 1.4px;
    font-size: 1.4em;
}
.links:hover{
    color: #e6c401;
}
.logoIcon{
    width: 250px;
    height: 100px;
    cursor: pointer;
}
.center{
    margin: auto 0;
}
.social{
    width: 60px;
    height: 60px;
    margin:5px;
    cursor: pointer;
}